import React, { useState } from "react";
// import DownloadIcon from "@mui/icons-material/Download";
import BlackLogo from "../assets/black_logo.png";
import ReorderIcon from "@mui/icons-material/Reorder";
import CloseIcon from "@mui/icons-material/Close";
import { capitalizeFirstLetter } from "../../utils/helper";

const Header = () => {
  const headeroptions = [
    {
      id: 1,
      Name: "about",
    },
    {
      id: 2,
      Name: "experience",
    },
    {
      id: 3,
      Name: "contact",
    },
  ];
  const [ShowHamburger, setShowHamburger] = useState(false);
  const [activeTab, setActiveTab] = useState("about");
  const activeTabHandler = (val) => {
    window.location.href = `#${val}`;
    setActiveTab(val);
  };

  // const DarkModeHandler = () => {};
  const HamburgerHandler = () => {
    setShowHamburger(!ShowHamburger);
  };

  return (
    <>
      <div className="header">
        <div className="header-logo">
          {/* <span className="header-logo-text"> &#8826; </span> */}
          <span className="header-logo-text">&lt;</span>
          <img src={BlackLogo} alt="Sajal Birla" />
          <span className="header-logo-text">/&gt;</span>
        </div>

        <div className="header-bar">
          {headeroptions.map((option, id) => (
            <div
              key={id}
              onClick={() => {
                activeTabHandler(option.Name);
              }}
              className={`header-bar-options  ${
                activeTab === option.Name && "header-bar-options-active"
              }`}
            >
              {capitalizeFirstLetter(option.Name)}
            </div>
          ))}
        </div>
        <div className="header-hamburger" onClick={HamburgerHandler}>
          {ShowHamburger ? (
            <CloseIcon fontSize="large" />
          ) : (
            <ReorderIcon fontSize="large" />
          )}
        </div>
        {ShowHamburger && (
          <div className="header-hamburger-dropdown ">
            <ul className="menu">
              {headeroptions.map((option, id) => (
                <li>
                  <div
                    key={id}
                    onClick={() => {
                      setShowHamburger(false);
                      activeTabHandler(option.Name);
                    }}
                    className={`header-bar-options  ${
                      activeTab === option.Name && "header-bar-options-active"
                    }`}
                  >
                    {capitalizeFirstLetter(option.Name)}
                  </div>
                </li>
              ))}
            </ul>
          </div>
        )}
        {/* <button className="header-button" onClick={DownloadResume}>
          <DownloadIcon /> Resume
        </button> */}
        {/* <button onClick={DarkModeHandler}>Dark Mode</button> */}
      </div>
    </>
  );
};
export default Header;
